@mixin input-box {
	background: var(--bg-color);
	border: $input-border-width solid $input-border-color;
	border-radius: $input-radius;
	color: v(color-secondary-08);
	display: block;
	font-size: v(font-size-scale-up-01);
	font-weight: v(font-weight-medium);
	height: var(--input-size);
	padding-left: v(spacing-scale-2x);
	padding-right: v(spacing-scale-2x);
	width: 100%;

	&::placeholder {
		color: var(--text-color);
		font-size: var(--font-size-scale-base);
		font-style: italic;
		font-weight: var(--font-weight-regular);
		opacity: 0.75;
	}

	&:not(:disabled) {
		@include hover;
		@include focus;

		&:focus,
		&:focus-visible,
		&.focus-visible {
			@include focus-shadow;
		}
		&:hover {
			--interactive-rgb: var(--rgb-secondary-08);
			--hover: var(--surface-opacity-xs, 0.3);
		}
	}

	&.has-icon {
		padding-right: v(spacing-scale-5x);
	}

	&[type="password"]::-ms-reveal,
	&[type="password"]::-ms-clear {
		display: none;
	}
}

@mixin input-states {
	@each $color in success, danger, warning, info {
		&.#{$color},
		&[data-#{$color}] {
			input {
				border: 2px solid var(--color-#{$color});
			}
			.feedback.color-#{$color} {
				align-items: center;
				background: $color;
				color: var(--color-secondary-01) !important;
				display: inline-flex;
				height: 35px;
				margin-bottom: var(--spacing-scale-base);
				padding: 5px;
			}
			@if ($color == warning) {
				.feedback.color-warning {
					color: var(--color-secondary-08) !important;
				}
			}
		}
	}
}

@mixin input-sizes {
	--input-small: 48px;
	--input-medium: 40px;
	--input-large: 32px;
	--input-size: var(--input-medium);

	@each $size in "small", "medium", "large" {
		&.#{$size},
		&[data-#{$size}] {
			--input-size: var(--input-#{$size});
		}
	}
}
