@import "../../partial/scss/base";

@mixin input-init {
	@include input-appearence;
	@include input-icon;
	@include input-colors;
	@include input-densities;
}
@mixin input-appearence {
	position: relative;
	label {
		display: inline-block;
		padding-bottom: var(--spacing-scale-half);
	}
	input {
		@include input-box;
	}
}
@mixin input-icon {
	&.has-icon {
		.#{$prefix}button.circle {
			float: right;
			margin-right: var(--spacing-scale-half);
			margin-top: calc(
				(var(--button-size) + var(--spacing-scale-half)) * -1
			);
		}
		&.small {
			.#{$prefix}button.circle {
				margin-top: calc(
					(var(--button-size) + var(--spacing-scale-base)) * -1
				);
			}
		}
		&.large {
			.#{$prefix}button.circle {
				margin-top: calc(var(--button-size) * -1);
			}
		}
	}
}
@mixin input-colors {
	@include input-states;
	input {
		&[disabled] {
			background-color: var(--color-secondary-03);
			border: 1px solid var(--color-secondary-06);
			color: var(--color-secondary-06);
		}
	}
}
@mixin input-densities {
	@include input-sizes;
}

.#{$prefix}input {
	@include input-init;
}
